import { createSlice } from '@reduxjs/toolkit';
import Axios from "axios";
import { getHeaders } from '../../helpers.js';

/* Just a test code... remove it afterwards */ 
const URI = "http://localhost:7000";//process.env.DEV_MONGO_URL;
/* Just a test code... remove it afterwards */

const initialState = {
    challenges: [],
    reflections: []
};

export const momentSlice = createSlice({
    name: 'moment',
    initialState,
    reducers: {
        addMoment: (state,action) => {
            state.noteList = [...state.noteList, action.payload]
        },
        updateReflectionList: (state,action) => {
            state.reflections = action.payload
        }
    }
});

export const {
    addMoment,
    updateReflectionList
} = momentSlice.actions;

export const actionAddMoment = moment => (dispatch,getState) => {
    const {user} = getState();
    const userId = user.userData ? user.userData._id : null;

    const headers = getHeaders();
    if(headers == null) return

    Axios.put(
        `${URI}/moments/add-moment`,
        {
            ...moment,
            userId
        },
        headers
    ).then( res => {
        dispatch(addMoment(res.data))
    })
}

export const actionGetChallenges = () => async (dispatch,getState) => {
    const headers = getHeaders();
    if(headers == null) return

    Axios.get(
        `${URI}/moments/get-challenges`,
        headers
    ).then( res => {
        console.log("actionGetChallenges")
        console.log(res)
    })
}

export const actionGetReflections = () => (dispatch,getState) => {
    const headers = getHeaders();
    if(headers == null) return

    Axios.get(
        `${URI}/moments/get-reflections`,
        headers
    ).then( res => {
        // console.log("actionGetReflections")
        dispatch(updateReflectionList(res.data.docs))
        // console.log(res.data.docs)
    })
}

export default momentSlice.reducer;